= 'ok' && env.msg !== '未检查' && env.msg !== '检查中...'}\"></span>\r\n                    </div>\r\n                </div>\r\n                <spinner id=\"spinner-box\" :size=\"md\" :fixed=\"true\" \r\n                     text=\"配置部署中，请稍后...\" v-ref:spinner>\r\n                </spinner>\r\n            </form>\r\n        </div>\r\n        <div slot=\"modal-footer\" class=\"modal-footer\">\r\n            <button type=\"button\" class=\"btn btn-default\" @click=\"testFn\">检查</button>\r\n            <button type=\"button\" class=\"btn btn-default\" @click=\"setFn\">部署</button>\r\n            <button type=\"button\" class=\"btn btn-default\" @click='testModal = false'>关闭</button>\r\n        </div>\r\n    </modal>\r\n</template>\r\n\r\n<script>\r\nimport { modal, spinner } from 'vue-strap'\r\n\r\nlet origin = {\r\n        testModal: false,\r\n        idNum: null,\r\n        envList: {}\r\n    };\r\n\r\nexport default {\r\n    data () {\r\n        return origin\r\n    },\r\n    methods: {\r\n\r\n        // 校验方法\r\n        testFn () {\r\n            for (let key in this.envList) {\r\n\r\n                let len = this.envList[key].length,\r\n                    currentIndex = 0,\r\n                    _this = this\r\n\r\n                const newRequest = (currentIndex, len) => {\r\n                    if (currentIndex >= len) {\r\n                        return\r\n                    }\r\n\r\n                    _this.envList[key][currentIndex].msg = '检查中...'\r\n\r\n                    _this.$http({\r\n                        url: '/instance_check/',\r\n                        method: 'POST',\r\n                        data: {\r\n                            id: _this.idNum,\r\n                            rid: _this.envList[key][currentIndex].id\r\n                        }\r\n                    })\r\n                    .then(response => {\r\n                        if (response.data.result !== 0) {\r\n                            _this.envList[key][currentIndex].msg = response.data.msg\r\n                        } else {\r\n                            _this.envList[key][currentIndex].msg = '服务器出错了'\r\n                        }\r\n\r\n                        currentIndex ++\r\n\r\n                        // 递归调用自身\r\n                        newRequest(currentIndex, len)\r\n                    })\r\n                }\r\n\r\n                newRequest(currentIndex, len)\r\n            }\r\n        },\r\n\r\n        // 部署方法\r\n        setFn () {\r\n            let isPass = true\r\n\r\n            for (let key in this.envList) {\r\n                let arr = this.envList[key]\r\n\r\n                arr.forEach((e) => {\r\n                    if (e.msg !== 'ok') {\r\n                        isPass = false\r\n                    }\r\n                })\r\n            }\r\n\r\n            if (isPass) {\r\n                this.$refs.spinner.show()\r\n\r\n                this.$http({\r\n                    url: '/instance_release/',\r\n                    method: 'POST',\r\n                    data: {\r\n                        id: this.idNum\r\n                    }\r\n                })\r\n                .then((response) => {\r\n                    this.$refs.spinner.hide()\r\n\r\n                    if (response.data.result === 1) {\r\n                        this.testModal = false\r\n\r\n                        this.$dispatch('show-success')\r\n                    } else {\r\n                        this.$dispatch('show-error', response.data.msg)\r\n                    }\r\n                })\r\n            } else {\r\n                this.$dispatch('show-notify', '存在未通过检测的配置')\r\n            }\r\n        }\r\n    },\r\n    components: {\r\n        modal,\r\n        spinner\r\n    },\r\n    events: {\r\n        'showTestEnv' (param) {\r\n            this.testModal = true\r\n\r\n            this.$http({\r\n                url: '/instance_check/?id=' + param,\r\n                method: 'GET'\r\n            })\r\n            .then(response =>