<template>
    <modal :show.sync="nginxModal" effect="fade" width="550px">
        <div slot="modal-header" class="modal-header">
            <h4 class="modal-title">Nginx配置</h4>
        </div>
        <div slot="modal-body" class="modal-body">
            <form class="form-horizontal form-small">
                <div class="form-group input-box">
                    <label class="control-label col-sm-3">Nginx版本：<span class="text-danger">*</span></label>
                    <div class="col-sm-8">
                        <v-select :value.sync="type" :options="types" placeholder="请选择">
                        </v-select>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-3">日志目录：<span class="text-danger">*</span></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" v-model="logPath">
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-3">配置文件目录：<span class="text-danger">*</span></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" v-model="docPath">
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-3">nginx端口号：<span class="text-danger">*</span></label>
                    <div class="col-sm-8">
                        <input type="number" min="0" max="99999" class="form-control" v-model="nginxPort">
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-3">nginx配置目录：<span class="text-danger">*</span></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control" v-model="nginxConf">
                    </div>
                </div>
            </form>
        </div>
        <div slot="modal-footer" class="modal-footer">
            <button type="button" class="btn btn-default">部署</button>
            <button type="button" class="btn btn-default" @click='nginxModal = false'>取消</button>
        </div>
    </modal>
</template>

<script>
import { modal } from 'vue-strap'
import vSelect from '../../global/Select.vue'

let origin = {
        nginxModal: false,
        types: [
            {value: '1.4.4', label: '1.4.4'}
        ]
    },
    init = Object.assign({}, origin)

export default {
    data () {
        return origin
    },
    components: {
        modal,
        vSelect
    },
    events: {
        'showNginx' (param) {
            this.nginxModal = true
        }
    },
}
</script>

<style>
.form-small {
    font-size: 12px;
}

.form-small .form-group {
    margin-bottom: 8px;
}
</style>


